.header {
  color: white;
  background-color: #2b2b2b;
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: space-around;
  padding: 0 400px;
  top: 0;
  width: calc(100% - 800px);
  height: 50px;
  z-index: 2;

  &__settings {
    color: white;
  }

  &__buttons {
    display: flex;
    align-items: center;
  }

  &__button {
    margin: 0;
    background: none;
    border: none;
  }

  &__icon {
    width: 40px;
  }

  &__icon_pause {
    width: 50px;
  }

  &__input {
    max-width: 40px;
    text-align: center;
    margin-left: 10px;
  }
}
